.parent-div {

    align-items: center;
    text-align: center;

}

.parent-div h1 {
    margin: 2%;
    color: midnightblue;
    font-size: 2.5rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

.parent-div p {
    margin: 0;
    text-align: center;
    font-size: 1rem;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: gray
}

.parent-div hr {
    width: 20%;
    height: 5px;
    overflow: visible;
    text-align: center;
    border: none;
    border-top: 2px double black;
    background-color: black;
}

/* line break */
.parent-div hr:after {
    background: var(--dark-orange);
    content: " ";
    padding: 0 10px;
    scroll-padding-block-start: 20px;
    position: relative;
    top: -13px;
}

.hire-btn {
    background-color: #090b24;
    color: white;



}

.get-btn {
    padding-left: 10px;
    margin-left: 10%;
    background-color: var(--dark-orange);
    color: white;
}

.get-btn:hover {
    background-color: cornsilk;
    color: #111;
}

/* about me box */




#get-started-page .section1 {

    box-shadow: 10px 10px 50px 1px grey;
    margin-bottom: 10%;
    border-radius: 10px;

}


.image-container {
    border-radius: 10px;
    padding-left: 0px;
    margin-left: 0px;
}

.detail-box {

    margin-left: 20px;
    margin-right: 20px;
    padding-bottom: 10%;
    padding-top: 10%;
    font-size: 15px;
    display: flex;
    flex-direction: column;
    text-align: left;


}

.detail-box p {

    text-align: justify;
    text-overflow: hidden;
    overflow: hidden;
}

.image-container img {

    width: 100%;
    height: 100%;
    border-radius: 10px 0px 0px 10px;
}

.profile-options {

    border: 10px black;
    margin-top: 200px;
}