#get-started-page .section1 {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 6fr));

    margin: auto;
    max-width: 70%;

}

.nav-title {
    display: grid;
    box-shadow: 20px;
    background: linear-gradient(to left, white 90%, midnightblue 10%) right;

}

.nav-title button {
    border-color: white;

    border-radius: 40px;
    text-align: left;
    padding: 20px;
    margin: 8px;
    color: black;
    background: linear-gradient(to left, white 50%, midnightblue 50%) right;
    background-size: 182.56%;
    transition: .5s ease-out;
    border: none;
}

.nav-title button:focus {
    color: white;
    background-position: left;

}

.nav-contents {
    display: justify;
    justify-content: space-around;

    text-align: left;
}

.nav-contents p {
    justify-content: space-evenly;
    text-align: left;
}