.parent-box1 {
    display: grid;
    grid-template-columns: auto auto;
    width: 80%;
     height: max-content;
    margin-left: 10%;
    align-items: left;
    text-align: left;
    background-color:midnightblue;
    border-radius: 20px;
    box-shadow: 10px 10px 50px 1px grey;
    padding-left: 20px;



}
.img-box  h1 {
    font-weight: bold;
    color: whitesmoke;
  
    margin-bottom: 0px;
}
.img-box span{
    color: white;
  font-size: 1.5rem;
}
.img-box img{
   
   min-height: 40%; 
   width: 400px;
}
.icon-box i{
    color: white;
   padding: 10px;
   font-size: 20px;
   margin-top: 0%;
    
}
.icon-box i:hover{
    transform: scale(1.5);
}
.form-box {
    margin-top: 100px;
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 15px;
    background-color: whitesmoke;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    width: 550px;
    height: max-content;
}

.box4{
    border-radius: 5px;
    background-color: lightgrey;
    border: white;
    padding: 10px;
    width:530px;
}

.btn{
    margin-top: 10px;
}
.hire-btn:hover{
 color: var(--dark-orange);
 background-color: antiquewhite;
}
.text{
font-size: large;
}
@media screen and (max-width:1110px){
.parent-box1{
    
        background-color: aliceblue;
}
.form-box{
    width: 70%;
   max-width: 200px;
    height: auto;
}
}