.profile-container{
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 10%;
    min-height: fit-content;
    
}
.profile-parent{
    display: flex;
    text-align: center;
    align-items: center;
    color:aliceblue;
}
.profile-detail-name{
    font-size: 1rem;
    font-family: "Poppins Semibold";

}
.profile-detail-role{
    display: flex;
   
    margin: 14px 0 24px 0;
}
.profile-detail-role h1{
    font-size: 40px;
    font-family: "Cursive";
}
.primary-text{
    color: (--white);
}
.highlighted-text{
    color: var(--dark-orange);
}
.profile-role-tagline{
    font-size: 19px;
    margin: 5px 0 0 0 ;
    font-family: "Poppins Light";
}
.highlighted-btn{
    margin-left :20px;
}
.primary-btn:hover{
    margin: 0px 20px 0px 0px;
}
  .highlighted-btn:hover{
    margin: 0px 0px 0px 40px;
    background-color: cornsilk;
    color: #111;
}
.profile-picture{
    display: flex;
    align-items : center;
    justify-content: center;
    box-shadow:0px 1px 0px  0px var(--white);
    border-radius: 50%;
    height: 380px;
    width: 300px;
    margin : 0 0px 0px 100px;
}
.profile-picture-background{
    height: 92%;
    width: 92%;
    background-image: url(../../assets/Home/profilePhoto.jpg.jpg);
    background-size: cover;
    border-radius: 50%;
     background-repeat: no-repeat;
     background-position: center;

}
.profile-picture-background:hover{
    transform: scale(1.07);
    transition: 1s ease-in-out;
}
.colz .colz-icon i {
    
    color: white;
    padding: 10px;
    padding-left: 0px;
    
}
.colz .colz-icon i:hover{
    transform: scale(1.5);
}
@media screen and (max-width:1110px){
    .profile-picture{
         margin: 0 0 0 80px;
    }
    .profile-detail-name{
        font-size: 20px;
    }

    .profile-detail-role .primary-text{
        font-size:12px;
    }
    .profile-option .btn{
        width: 120px;
        padding: 11px 0;
        z-index : 700;

    }
}
@media screen and (max-width:910px){
 .profile-picture{
    height: 320px;
    width: 320px;
    margin: 0 0 0 4px;
 }

}
@media screen and (max-width:810px){
.profile-picture{
    margin: 0 !important;

}
.profile-parent{
    flex-direction: column-reverse;
    margin: 40px 0 0 0 ;

}
.profile-options{
    display: flex;
    justify-content: space-between;

}
.profile-details{
margin: 25px 0 0 ;
}
.profile-options, .highlighted-btn{
    margin : 0 !important;

}
.profile-options, .profile-details-role{
text-align: center;
}
@media screen and (max-width:375px){
    .profile-picture{
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }
    .profile-details{
        width: 90%;
        
    }
}
}